.logo {
    width: 60px;
    height: 16px;
    background-color: transparent;

    &__fro {
        fill: var(--primary-color);
    }

    &__heart {
        fill: var(--secondary-color);
    }
}

@media (min-width: 1220px) {
    .logo {
        margin-right: 25px;
        margin-top: 11px;
    }
}